nav {
  height: 80px;
  .nav-logo {
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.nav-list {
  // margin: 0 85px;
  .nav-menu {
    li {
      &:not(:last-child) {
        a {
          position: relative;
          // display: block;
          &:hover {
            font-weight: bolder; /*a*/
            color: $success;
          }
          /*a*/
          &:after {
            content: "";
            position: absolute;
            left: 50%; /*50%0*/
            right: 50%; /*50%0*/
            bottom: -5px;
            height: 0;
            border-bottom: 2px solid $success;
            transition: 0.5s; /*0.5*/
          }
          &:hover:after {
            left: 0; /*50%0*/
            right: 0; /*50%0*/
          }
        }
      }
    }
  }
}
/* PADmenu */
.nav-md-menu {
  display: none;
  background-color: $secondary;
  @include pad {
    display: block;
    max-height: 0px;
    overflow: hidden;
    transition: max-height 1.5s;
    position: absolute;
    z-index: 100;
    // border
    top: 79px;
    left: 0;
    right: 0;
  }
  a {
    @include pad {
      display: block;
      text-align: center;
      &:hover {
        color: $light !important;
        background-color: $warning;
      }
    }
  }
}
/* PADmenu */
.menu-show .nav-md-menu {
  @include pad {
    height: auto;
    max-height: 400px;
  }
}

.cart-icon {
  position: relative;
  &:after {
    position: absolute;
    content: "3";
    width: 22px;
    height: 18px;
    background-color: $warning;
    border-radius: 50%;
    font-size: 12px;
    color: $light;
    text-align: center;
    top: -8px;
    left: 13px;
  }
}
