.footer {
  a {
    position: relative;
    // display: block;
    &:hover {
      font-weight: bolder; /*a*/
      color: $success;
    }
    /*a*/
    &:after {
      content: "";
      position: absolute;
      left: 50%; /*50%0*/
      right: 50%; /*50%0*/
      bottom: -5px;
      height: 0;
      border-bottom: 2px solid $success;
      transition: 0.5s; /*0.5*/
    }
    &:hover:after {
      left: 0; /*50%0*/
      right: 0; /*50%0*/
    }
  }
}
