.scrollTopBtn {
  text-decoration: none;
  position: fixed; /**/
  display: flex; /*^*/
  justify-content: center; /*^*/
  align-items: center; /*^*/
  width: 40px;
  height: 40px;
  right: 20px;
  bottom: 80px;
  border-radius: 50px;
  background: $success;
  &:hover {
    text-decoration: none;
    background: $primary;
  }
  i {
    color: $light;
  }
}
